import React from 'react';

class Form extends React.Component {
  state = {
    nameMedicine: '',
    amountMedicine: '',
    dateMedicine: new Date().toISOString().slice(0, 10),
    remindMedicine: false,
  }

  handleInputChange = e => {
    if (e.target.type === 'checkbox') {
      this.setState({
        [e.target.id]: e.target.checked,
      })
    } else {
      this.setState(({
        [e.target.id]: e.target.value,
      }))
    }
  }

  handleClickSubmit = () => {
    const { nameMedicine, amountMedicine, dateMedicine, remindMedicine } = this.state;
    const newMedicine = this.props.addMedicine(nameMedicine.trim(), dateMedicine, amountMedicine, remindMedicine);
    if (newMedicine) {
      this.setState({
        nameMedicine: '',
        amountMedicine: '',
        dateMedicine: new Date().toISOString().slice(0, 10),
        remindMedicine: false,
      })
    }
  }


  render() {
    return (
      <div className="wrapper">
        <form className="add-medicine">

          <label htmlFor="nameMedicine" className="add-medicine__name">
            <h2 className="add-medicine__name--title">Nazwa Leku:</h2>
            <input
              type="text"
              id="nameMedicine"
              className="add-medicine"
              placeholder="Nazwa"
              value={this.state.nameMedicine}
              onChange={this.handleInputChange}

            />
          </label>

          <label htmlFor="amountMedicine">
            <h2>Ilość Tabletek:</h2>
            <input
              type="number"
              id="amountMedicine"
              className="amountMedicine"
              placeholder="0"
              value={this.state.amountMedicine}
              onChange={this.handleInputChange}

            />
          </label>

          <label htmlFor="dateMedicine">
            <h2>Data ważności:</h2>
            <input
              type="date"
              id="dateMedicine"
              className="dateMedicine"
              value={this.state.dateMedicine}
              onChange={this.handleInputChange}

            />
          </label>

          <label htmlFor="remindMedicine">
            <h2>Przypomnienia:</h2>
            <input
              type="checkbox"
              id="remindMedicine"
              className="remindMedicine"
              checked={this.state.remindMedicine}
              onChange={this.handleInputChange}

            />
          </label>

        </form>

        <button
          onClick={this.handleClickSubmit}
        >
          Dodaj
        </button>
      </div>
    )
  }
}

export default Form;